.spinner-wrapper {
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.spinner {
  width: 80px;
  height: 80px;
  display: inline-block;
  position: relative;
}

.spinner div {
  opacity: 1;
  border: 4px solid #8b9eff9c;
  border-radius: 50%;
  animation: 1s cubic-bezier(0, .2, .8, 1) infinite lds-ripple;
  position: absolute;
}

.spinner div:nth-child(2) {
  animation-delay: -.5s;
}

@keyframes lds-ripple {
  0% {
    opacity: 0;
    width: 0;
    height: 0;
    top: 36px;
    left: 36px;
  }

  4.9% {
    opacity: 0;
    width: 0;
    height: 0;
    top: 36px;
    left: 36px;
  }

  5% {
    opacity: 1;
    width: 0;
    height: 0;
    top: 36px;
    left: 36px;
  }

  100% {
    opacity: 0;
    width: 72px;
    height: 72px;
    top: 0;
    left: 0;
  }
}

